import { getPage } from '~/api/posts'

export default {
  async asyncData() {
    const page = await getPage('basis-of-faith')
    return { page }
  },
  head() {
    return {
      title: this.page.meta_title,
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: this.page.meta_description
        }
      ]
    }
  }
}
